/* eslint-disable import/no-anonymous-default-export */
import React from "react";
// import ComFilhos from './components/ComFilhos';
// import ComParametro from './components/ComParametro';
// import Card from './components/layout/Card';
import Header from "./components/Layout/Header";
import ListaPresentes from "./components/Pages/ListaPresentes";
import Teste from "./components/Pages/Teste";
import Home from "./components/Pages/Home";
import Tema from "./components/Pages/Tema";
import Navbar from "./components/Navbar/Navbar";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";

export default (props) => (
	<div className="App">
		<Router>
			<Navbar />
			<Switch>
				<Route path="/" exact component={Home} />
				<Route path="/ChaBebe-Site" component={Home} />
				<Route path="/ChaBebe-Site/" component={Home} />
				<Route path="/services" component={Teste} />
				<Route path="/tema" component={Tema} />
				<Route path="/lista-presentes" component={ListaPresentes} />
			</Switch>
		</Router>
	</div>
);
